import React, {useEffect, useState} from "react";
import hoverEffect from "../hover-effect";
import overlay from "../../../../assets/displacement_image.png"
import image1 from "../../../../assets/Graphic_Design/kissan.png"
import image2 from "../../../../assets/Branding/graphic_design_image.jpg"


export default function Kisanjam(){
    const [imgSrc, setImgSrc] = useState(image1)
    useEffect(()=> {
        var image_animate = new hoverEffect({
          parent: document.querySelector(".item-image"),
          intensity: .3,
          image1: "https://preview.redd.it/dvbx71p4rdv71.jpg?width=960&crop=smart&auto=webp&s=b26b288178f12119d2e9e2eb7c274c25d8b4b3de",
          image2: "https://preview.redd.it/smkup4nmhfv71.png?width=960&crop=smart&auto=webp&s=c5c47cdae7ed6a9a57871299081588edda6aae91",
          displacementImage: overlay
        })
      })

    return(
        <div className = 'setUp'>
            <div>
                <div className="item-image">
                    <img style ={{
                    maxWidth:900,
                    margin:'0 auto'
                }} src={imgSrc} onMouseEnter={()=>{
                        setImgSrc(image2)
                    }}
                    onMouseLeave={()=>{
                        setImgSrc(image1)
                    }}></img>
                </div>
            </div>
        </div>
    )
}